



















import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import Alert from "./Alert.vue";

@Component({
  components: { Alert },
})
export default class createTags extends Vue {
  @Prop(Boolean) readonly value!: boolean;
  newTagName: string = "";
  visible: boolean = false;
  message: string = "";
  created() {
    this.$store.commit("fetchTags");
  }

  get tagList() {
    return this.$store.state.tagList;
  }

  cancel(): void {
    this.newTagName = "";
    this.$emit("update:value", false);
  }
  setAlert(alertmessage: string): void {
    this.visible = true;
    this.message = alertmessage;
    setTimeout(() => {
      this.visible = false;
      this.message = "";
    }, 800);
  }
  define(): void {
    const nameList = this.tagList.map((item: { name: string }) => item.name);
    const name = this.newTagName.trim();
    if (name === "") {
      this.setAlert("标签名不能为空");
    } else if (name.length > 10) {
      this.setAlert("标签名不能超过10个字符");
    } else {
      if (nameList.indexOf(name) >= 0) {
        this.setAlert("标签名已存在，请重新输入");
      } else {
        this.$store.commit("createTag", name);
        this.newTagName = "";
        this.$emit("update:value", false);
      }
    }
  }
}
