





















import Vue from "vue";
import { Component } from "vue-property-decorator";
import EditItem from "@/components/Money/EditItem.vue";
import Button from "@/components/Button.vue";
import GotoBack from "@/components/GotoBack.vue";
import Alert from "@/components/Alert.vue";
@Component({
  components: { GotoBack, Button, EditItem, Alert },
})
export default class EditLabel extends Vue {
  newName: string = "";
  visible: boolean = false;
  message: string = "";
  get tag() {
    return this.$store.state.currentTag;
  }
  setAlert(alertmessage: string): void {
    this.visible = true;
    this.message = alertmessage;
    setTimeout(() => {
      this.visible = false;
      this.message = "";
    }, 800);
  }
  created(): void {
    this.$store.commit("fetchTags");
    this.$store.commit("findTag", this.$route.params.id);
    if (!this.tag) {
      this.$router.replace("/404");
    } else {
      this.newName = this.tag.name;
    }
  }

  updateTag(name: string) {
    this.newName = name;
  }

  comfirm() {
    if (this.tag) {
      const name = this.newName.trim();
      if (name === "") {
        this.setAlert("标签名不能为空！");
      } else if (name.length > 10) {
        this.setAlert("标签名不能超过10个字符");
      } else if (this.tag.name === name) {
        this.$router.replace("/labels/");
      } else {
        this.$store.commit("updateTag", {
          id: this.tag.id,
          newName: name,
        });
        if (this.$store.state.newNameStatus === "duplicated") {
          this.setAlert("标签名已存在，请重新输入");
        } else {
          this.$router.replace("/labels/");
        }
      }
    }
  }

  remove() {
    if (this.tag) {
      this.$store.commit("removeTag", this.tag.id);
    }
  }
}
