
























import Vue from "vue";
import EditItem from "@/components/Money/EditItem.vue";
import NumberPad from "@/components/Money/NumberPad.vue";
import Tags from "@/components/Money/Tags.vue";
import { Component } from "vue-property-decorator";
import store from "@/store";
import Tabs from "@/components/Tabs.vue";
import typeList from "@/contants/typeList";
import Alert from "@/components/Alert.vue";
@Component({
  components: { Tabs, Tags, NumberPad, EditItem, Alert },
})
export default class Money extends Vue {
  // eslint-disable-next-line no-undef
  record: RecordItem = {
    tags: [],
    notes: "",
    type: "-",
    amount: 0,
    createdAt: new Date().toISOString(),
  };
  typeList = typeList;
  visible: boolean = false;
  message: string = "";
  setAlert(alertmessage: string): void {
    this.visible = true;
    this.message = alertmessage;
    setTimeout(() => {
      this.visible = false;
      this.message = "";
    }, 800);
  }
  created() {
    store.commit("fetchRecords");
  }
  mounted() {
    if (document.body.clientWidth < 500) {
      (document.querySelector(".Nav-wrapper") as HTMLElement).style.height =
        document.body.clientHeight.toString() + "px";
    }
  }

  // eslint-disable-next-line no-undef
  onSelectTags(value: Tag[]) {
    this.record.tags = value;
  }

  saveRecord() {
    if (!this.record.tags || this.record.tags.length === 0) {
      return this.setAlert("请至少选择一个标签");
    } else if (this.record.amount === 0) {
      return this.setAlert("请输入金额");
    }
    if (this.$store.state.createRecordError === null) {
      this.$store.commit("createRecord", this.record);
      this.setAlert("已保存");
      this.record.notes = "";
    }
  }
}
