
















import Vue from "vue";
import { Component } from "vue-property-decorator";
import * as echarts from "echarts";
import groupedList from "@/lib/groupedList";
import GotoBack from "@/components/GotoBack.vue";
import _ from "lodash";
import dayjs from "dayjs";

@Component({
  components: { GotoBack },
})
export default class Echart extends Vue {
  type: string = this.$route.query.type as string;
  typeToWord: string = this.type === "-" ? "支出" : "收入";
  totalMoney: number = 0;
  key: string[] | undefined;
  value: (number | undefined)[] | undefined;
  get dataList() {
    return groupedList(this.type);
  }

  get keyValueList() {
    const array = [];
    for (let i = 0; i <= 29; i++) {
      const dateString = dayjs(new Date())
        .subtract(i, "day")
        .format("YYYY-MM-DD");
      const found = _.find(this.dataList, {
        title: dateString,
      });
      array.push({ key: dateString, value: found ? found.total : 0 });
    }
    array.sort((a, b) => {
      if (a.key > b.key) {
        return 1;
      } else if (a.key === b.key) {
        return 0;
      } else {
        return -1;
      }
    });
    return array;
  }

  get option() {
    return {
      color: ["#232428"],
      grid: {
        left: 16,
        right: 80,
        top: 80,
      },
      tooltip: {
        trigger: "item",
        position: "top",
      },
      xAxis: {
        type: "category",
        axisTick: {
          alignWithLabel: true,
          inside: true,
        },
        axisLine: {
          show: false,
        },
        data: this.key,
      },
      yAxis: {
        type: "value",
        position: "right",
        splitLine: {
          show: false,
        },
      },
      series: [
        {
          data: this.value,
          type: "bar",
          showBackground: true,
          backgroundStyle: {
            color: "#efefef",
            borderRadius: [10, 10, 10, 10],
          },
          itemStyle: {
            borderRadius: 10,
          },
          label: {
            position: "top",
            show: true,
            distance: 10,
            fontWeight: "bold",
          },
        },
      ],
    };
  }

  countTotalMoney() {
    if (this.value) {
      this.value.map((item) => {
        if (item) {
          this.totalMoney += item;
        }
      });
    }
    this.totalMoney = Math.floor(this.totalMoney * 100) / 100;
  }

  mounted() {
    this.key = this.keyValueList.map((item) => item.key.substr(5, 5));
    this.value = this.keyValueList.map((item) => item.value);

    this.countTotalMoney();

    const el = this.$refs.container as HTMLElement;
    if (el) {
      let chart = echarts.init(el);
      this.option && chart.setOption(this.option);
    }
    (this.$refs.chartWrapper as HTMLElement).scrollLeft = el.scrollWidth;
    let divX: number = 0;
    el.onmousedown = function(event: MouseEvent) {
      divX = event.clientX - el.offsetLeft;
      document.onmousemove = function(event: MouseEvent) {
        el.style.left = event.clientX - divX + "px";
      };
      document.onmouseup = function() {
        document.onmousemove = null;
        document.onmouseup = null;
        return false;
      };
    };
  }
}
